import React from 'react';
import { skillsData } from '../data/skillsData';
import { motion } from 'framer-motion';

const Profil = () => {
    return (
        <div className='profil'>
            <h1>Ludovic Sagnal</h1>
            <h2>Dveloppeur Full Stack</h2>
            <img src="./img/prof.webp" alt="photo de porfil" />
            
            <svg className='profil-line' viewBox="0 0 800 400"><path d="M86.39759063720703,110.31389617919922C100.21373310089112,133.0493218612671,124.89534496307374,216.2376615524292,157.24960327148438,226.90582275390625C189.603861579895,237.5739839553833,219.9626042175293,158.02689727783203,252.31686401367188,165.0224151611328C284.67112380981445,172.0179330444336,276.9984805297852,247.04036415100097,323.16888427734375,262.7802734375C369.3392880249023,278.520182723999,416.68458435058596,242.5919174194336,489.0881652832031,245.73989868164062C561.4917462158203,248.88787994384765,654.4199781799316,272.45291275024414,694.4692993164062,278.92376708984375" fill="none" strokeWidth="1" stroke="url(&quot;#SvgjsLinearGradient1000&quot;)" strokeLinecap="round" strokeDasharray="1 6" strokeOpacity="1"></path><defs><linearGradient id="SvgjsLinearGradient1000" gradientTransform="rotate(0, 0.5, 0.5)"><stop stopColor="#50a5de" offset="0"></stop><stop stopColor="hsl(270, 73%, 53%)" offset="1"></stop></linearGradient></defs></svg>

            <p className='resume'>Passionn depuis toujours par le monde de l'informatique, j'ai dcid de me reconvertir vers le mtier de dveloppeur web au mois d'aot 2022. A la suite d'une formation russie et d'un grand travail d'autodidacte, je cherche aujourd'hui une entreprise qui saura accueillir avec bienveillance un dveloppeur junior avide d'apprendre et de contribuer pleinement  des projets captivants.
            </p>
            <h3>Comptences</h3>
            <ul className='skills-container'>
                <li>
                {skillsData
                    .filter((skill) => skill.type === 'front')
                    .map((skill, index) => (
                      <motion.div className='skills' key={index} whileHover={{y:5}} >
                          <img src={`./img/skills/${skill.img}`} alt={`matrise de ${skill.name}`}/>
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
                <li>
                {skillsData
                    .filter((skill) => skill.type === 'back')
                    .map((skill, index) => (
                      <motion.div className='skills' key={index} whileHover={{y:5}} >
                          <img src={`./img/skills/${skill.img}`} alt={`matrise de ${skill.name}`}/>
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
                <li>
                {skillsData
                    .filter((skill) => skill.type === 'other')
                    .map((skill, index) => (
                      <motion.div className='skills' key={index} whileHover={{y:5}}>
                          <img src={`./img/skills/${skill.img}`} alt={`matrise de ${skill.name}`}/>
                          <p className='skills-label'>{skill.name}</p>
                      </motion.div>
                    ))}
                </li>
            </ul>
        </div>
    );
};

export default Profil;